/* ColorPicker.css */
.color-picker {
  display: inline-block;
  text-align: center;
  margin: 10px;
}

button {
  font-size: 19px;
  padding: 10px 20px;
  background-color: green;
  color: white;
  border: none;
  cursor: pointer;
}

.color-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 10px;
}

.color-item {
  width: 50px;
  height: 50px;
  margin: 5px;
  cursor: pointer;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5); /* Add box shadow here */
}

.selected-color {
  margin-top: 10px;
  color: black;
}

.selected-color span {
  display: inline-block;
  padding: 5px;
  color: white;
}
